.App-logo {
  height: 5vmax;
  pointer-events:painted;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  font-family:cursive;
  color: white;
  margin-bottom: 50px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button-style1 {
  background-color: aqua;

}
.chat-container {
  display: flex;
  flex-direction: column;
  height: 70vh;
  max-width: 800px;
  margin:0  auto;
  padding: 20px;
}

.messages-container {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.message {
  margin: 10px 0;
  padding: 10px 15px;
  border-radius: 18px;
  max-width: 70%;
}

.message.user {
  align-self: flex-end;
  background-color: #007bff;
  color: white;
}

.message.ai {
  align-self: flex-start;
  background-color: #f1f1f1;
  color: #333;
}

.message-form {
  display: flex;
  gap: 10px;
}

.messages-container {
  display: flex;
  flex-direction: column;}

.message-form input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 20px;
}

.message-form button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.message-form button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}